.contextMenu {
    position: fixed;
    z-index: 101;
    background: #fff;
    padding-top: 2px;
    padding-bottom: 2px;
    box-shadow: 0px 2px 10px var(--ct-color-subtle-copy)
}

.contextMenuOption {
    padding: 5px 15px 5px 15px;
    min-width: 220px;
    font-size: 13px;
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    border: none
}

.contextMenuOption:hover,
.contextMenuOption:active {
    background-color: var(--ct-color-link);
    color: #fff
}

.xterm {
    position: relative;
    user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none
}

.xterm.focus,
.xterm:focus {
    outline: none
}

.xterm .xterm-helpers {
    position: absolute;
    top: 0;
    z-index: 5
}

.xterm .xterm-helper-textarea {
    padding: 0;
    border: 0;
    margin: 0;
    position: absolute;
    opacity: 0;
    left: -9999em;
    top: 0;
    width: 0;
    height: 0;
    z-index: -5;
    white-space: nowrap;
    overflow: hidden;
    resize: none
}

.xterm .composition-view {
    background: #000;
    color: #FFF;
    display: none;
    position: absolute;
    white-space: nowrap;
    z-index: 1
}

.xterm .composition-view.active {
    display: block
}

.xterm .xterm-viewport {
    background-color: #000;
    overflow-y: scroll;
    cursor: default;
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0
}

.xterm .xterm-screen {
    position: relative
}

.xterm .xterm-screen canvas {
    position: absolute;
    left: 0;
    top: 0
}

.xterm .xterm-scroll-area {
    visibility: hidden
}

.xterm-char-measure-element {
    display: inline-block;
    visibility: hidden;
    position: absolute;
    top: 0;
    left: -9999em;
    line-height: normal
}

.xterm {
    cursor: text
}

.xterm.enable-mouse-events {
    cursor: default
}

.xterm.xterm-cursor-pointer,
.xterm .xterm-cursor-pointer {
    cursor: pointer
}

.xterm.column-select.focus {
    cursor: crosshair
}

.xterm .xterm-accessibility,
.xterm .xterm-message {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 10;
    color: transparent
}

.xterm .live-region {
    position: absolute;
    left: -9999px;
    width: 1px;
    height: 1px;
    overflow: hidden
}

.xterm-dim {
    opacity: 0.5
}

.xterm-underline {
    text-decoration: underline
}

.xterm-strikethrough {
    text-decoration: line-through
}

.console-ct {
    font-family: Menlo, Monaco, Consolas, monospace;
    margin-top: 0;
    margin-bottom: 0;
    font-size: var(--pf-global--FontSize--md);
    text-align: center;
    line-height: normal
}

@media(min-width: 568px) {
    .console-ct {
        font-size: var(--pf-global--FontSize--sm)
    }
}

.console-ct>pre {
    text-align: left;
    display: block;
    font-family: inherit;
    font-size: inherit;
    width: 48em;
    height: 310px;
    overflow-y: scroll;
    white-space: pre-wrap;
    margin: 0 auto
}

.console-ct>.terminal {
    display: flex;
    height: 100%;
    align-items: center;
    padding-left: var(--pf-global--spacer--xs)
}

.terminal .terminal-cursor {
    border: 1px solid var(--pf-global--palette--black-200)
}

.terminal:focus .terminal-cursor {
    border: none;
    animation: blink 1s step-end infinite
}

@keyframes blink {
    from {
        color: var(--ct-color-text);
        background: var(--pf-global--palette--black-200)
    }

    50% {
        color: var(--pf-global--palette--black-200);
        background: var(--ct-color-text)
    }
}

.xterm-screen,
.xterm-viewport {
    width: auto !important
}

